<template>
    <div class="school-search-wrapper">
        <div class="banner">
            <div class="search-panel">
                <i></i>
                <input type="text" v-model="queryTerm" placeholder=" /  / ">
            </div>
        </div>
        <div class="school-search-main">
            <ul class="filter-wrapper">
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.area.title}} :</span>
                    <v-checkbox v-model="filterItems.area.allChecked"
                    @click="checkAll(filterItems.area)"></v-checkbox>
                    <v-checkbox-group :data="filterItems.area.items"
                    v-model="filterItems.area.checkItems"
                    @change="setState(filterItems.area)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.schoolType.title}} :</span>
                    <v-checkbox v-model="filterItems.schoolType.allChecked"
                    @click="checkAll(filterItems.schoolType)"></v-checkbox>
                    <v-checkbox-group :data="filterItems.schoolType.items"
                    v-model="filterItems.schoolType.checkItems"
                    @change="setState(filterItems.schoolType)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.studyForm.title}} :</span>
                    <v-checkbox v-model="filterItems.studyForm.allChecked"
                    @click="checkAll(filterItems.studyForm)"></v-checkbox>
                    <v-checkbox-group :data="filterItems.studyForm.items"
                    v-model="filterItems.studyForm.checkItems"
                    @change="setState(filterItems.studyForm)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.ageGroup.title}} :</span>
                    <v-checkbox v-model="filterItems.ageGroup.allChecked"
                    @click="checkAll(filterItems.ageGroup)"></v-checkbox>
                    <v-checkbox-group :data="filterItems.ageGroup.items"
                    v-model="filterItems.ageGroup.checkItems"
                    @change="setState(filterItems.ageGroup)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.schoolAge.title}} :</span>
                    <v-select class="age-select" size="sm" style="width: 75px;"
                    :data="filterItems.schoolAge.bOptions"
                    v-model="filterItems.schoolAge.minVal"
                    @change="judgeVal(filterItems.schoolAge)"></v-select>
                    <span class="line"></span>
                    <v-select size="sm" style="width: 75px;"
                    :data="filterItems.schoolAge.eOptions"
                    v-model="filterItems.schoolAge.maxVal">
                        <template slot-scope="{data}">
                            {{data.label}}
                        </template>
                    </v-select>
                </li>
            </ul>
            <div class="result-wrapper">
                <div class="result-panel">{{listTotal}}</div>
                <div class="compare-panel">
                    <span class="title" v-if="compareItems.length > 0"></span>
                    <span class="compare-item" v-for="ci in compareItems" :key="ci.id">{{ci.schoolcn}}<i class="delete-icon" @click="cancelContrast(ci)"></i></span>
                    <router-link tag="button" class="compare-btn" to="/school-search-result"></router-link>
                </div>
            </div>
            <div class="school-wrapper">
                <ul class="school-list">
                    <li class="school-item" v-for="sl in schoolList" :key="sl.id">
                        <div class="school-logo" @click.stop="getSchoolDetail(sl)"><img :src="'http://47.52.167.136:1328' + sl.logo" alt=""></div>
                        <div class="school-info">
                            <h2 class="school-name" v-tooltip.topleft='sl.schoolcn+ " " +sl.schoolen' @click.stop="getSchoolDetail(sl)">{{sl.schoolcn}} {{sl.schoolen}}</h2>
                            <div class="school-tags">
                                <span class="tag" v-for="t in sl.tag" :key="t.id" :class="t.type">{{t.result}}</span>
                            </div>
                            <p>{{sl.schoolcharacter}}</p>
                            <button class="contrast-btn" v-if="!sl.isCompare" @click="addContrast(sl)"></button>
                            <button class="contrast-btn cancel-status" @click="cancelContrast(sl)" v-else></button>
                        </div>
                    </li>
                </ul>
                <div class="map-panel">
                    <baidu-map class="map-view" :center="center" :zoom="zoom" @ready="mapHandler" :scroll-wheel-zoom="true">
                        <bm-marker v-for="b in locateArr" :key="b.name" :position="{lng: b.lng, lat: b.lat}">
                            <bm-label :content="b.name" :labelStyle="{fontSize : '10px'}" :offset="{width: -35, height: 30}"/>
                        </bm-marker>
                    </baidu-map>
                </div>
            </div>
            <div class="pagination-wrapper">
                <v-pagination show-quick-jumper v-model="listVal" @change="jumpPage" :total="listTotal"></v-pagination>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            queryTerm: '',
            filterItems: {
                area: {
                    title: '',
                    allChecked: false,
                    items: [],
                    checkItems: []
                },
                schoolType: {
                    title: '',
                    allChecked: false,
                    items: [],
                    checkItems: []
                },
                studyForm: {
                    title: '',
                    allChecked: false,
                    items: [],
                    checkItems: []
                },
                ageGroup: {
                    title: '',
                    allChecked: false,
                    items: [],
                    checkItems: []
                },
                schoolAge: {
                    title: '',
                    bOptions: [],
                    eOptions: [],
                    minVal: '',
                    maxVal: ''
                }
            },
            hotareaid: '',
            schooltypeid: '',
            studyformid: '',
            agesectionid: '',
            startage: '',
            endage: '',
            schoolList: [],
            compareItems: [],
            listTotal: 0,
            listVal: 1,
            locateArr: [],
            center: {
                lng: 0,
                lat: 0
            },
            zoom: 200
        }
    },
    created(){
        let that = this

        // 
        this.axios.get(this.HOST + '/schoolHotArea/list?limit=10&offset=0').then((response) => {
            let arr = that.filterItems.area.items
            arr.splice(0, arr.length)
            for(let a of response.data.rows){
                let obj = {
                    label: a.hotareacn,
                    value: a.id
                }
                arr.push(obj)
            }
        })

        // 
        this.axios.get(this.HOST + '/schoolType/list?limit=10&offset=0').then((response) => {
            let arr = that.filterItems.schoolType.items
            arr.splice(0, arr.length)
            for(let b of response.data.rows){
                let obj = {
                    label: b.schooltypecn,
                    value: b.id
                }
                arr.push(obj)
            }
        })

        // 
        this.axios.get(this.HOST + '/schoolStudyForm/list?limit=10&offset=0').then((response) => {
            let arr = that.filterItems.studyForm.items
            arr.splice(0, arr.length)
            for(let c of response.data.rows){
                let obj = {
                    label: c.studyformcn,
                    value: c.id
                }
                arr.push(obj)
            }
        })

        // 
        this.axios.get(this.HOST + '/schoolAgeSection/list?limit=10&offset=0').then((response) => {
            let arr = that.filterItems.ageGroup.items
            arr.splice(0, arr.length)
            for(let d of response.data.rows){
                let obj = {
                    label: d.agesectioncn,
                    value: d.id
                }
                arr.push(obj)
            }
        })

        // 
        this.axios.get(this.HOST + '/schoolAge/list?limit=10&offset=0').then((response) => {
            let sage = parseInt(response.data.rows[0].startage)
            let eage = parseInt(response.data.rows[0].endage)
            let arr = that.filterItems.schoolAge.bOptions
            let arr1 = that.filterItems.schoolAge.eOptions
            arr.splice(0, arr.length)
            arr1.splice(0, arr1.length)
            for(let e=sage; e<eage; e++){
                let obj = {
                    label: e+'',
                    value: e
                }
                arr.push(obj)
                arr1.push(obj)
            }
        })

        // 
        this.getSchoolList()
    },
    mounted(){
        if(sessionStorage.getItem('compareList') && sessionStorage.getItem('compareList') !== 'undefined'){
            this.compareItems = JSON.parse(sessionStorage.getItem('compareList'))
        }
    },
    methods: {
        checkAll(obj){
            if(obj.checkItems.length == obj.items.length){
                obj.checkItems = []
                obj.allChecked = false
            }else{
                let temp = JSON.parse(JSON.stringify(obj.items))
                obj.checkItems.splice(0, obj.checkItems.length)
                for(let i = 0; i < temp.length; i++){
                    obj.checkItems.push(temp[i].value)
                }
                obj.allChecked = true
            }
        },
        setState(obj){
            if(obj.checkItems.length == obj.items.length){
                obj.allChecked = true
            }else{
                obj.allChecked = false
            }
        },
        judgeVal(obj){
            obj.maxVal = ''
            let min = obj.minVal
            let arr = obj.eOptions
            let that = this
            for(let i = 0; i < obj.eOptions.length; i++){
                if(obj.eOptions[i].value < min){
                    that.$set(that.filterItems.schoolAge.eOptions[i], 'disabled', true)
                }
            }
        },
        addContrast(o){
            o.isCompare = true
            this.compareItems.push(o)
            this.setCompareSession()
        },
        cancelContrast(o){
            let arr = this.compareItems
            if(arr.length > 0){
                o.isCompare = false
                for(let j=0; j<arr.length; j++){
                    if(arr[j] == o){
                        arr.splice(j, 1)
                    }
                }
            }
            this.setCompareSession()
        },
        getSchoolList(idx = 0){
            let that = this
            let hotareaid = this.hotareaid ? '&hotareaid=' + this.hotareaid : ''
            let schooltypeid = this.schooltypeid ? '&schooltypeid=' + this.schooltypeid : ''
            let studyformid = this.studyformid ? '&studyformid=' + this.studyformid : ''
            let agesectionid = this.agesectionid ? '&agesectionid=' + this.agesectionid : ''
            let startage = this.startage ? '&startage=' + this.startage : ''
            let endage = this.endage ? '&endage=' + this.endage : ''

            this.axios.get(this.HOST + '/school/list?limit=10&offset=' + idx + hotareaid + schooltypeid + studyformid + agesectionid + startage + endage).then((response) => {
                console.log(response.data.rows)
                that.schoolList = response.data.rows
                that.listTotal = response.data.total
                
            }).catch((error) => {
                console.log(error)
            })
        },
        getLocate(arr){
            this.locateArr.splice(0, this.locateArr.length)
            for(let l of arr){
                let loc = {
                    name: l.schoolcn+ ' ' +l.schoolen,
                    lng: l.lng,
                    lat: l.lat
                }
                this.locateArr.push(loc)
            }
        },
        jumpPage(i){
            i = i-1
            this.getSchoolList(i)
        },
        getSchoolDetail(obj){
            this.$router.push(
                {
                    path: 'school-detail',
                    query: {
                        id: obj.id
                    }
                }
            )
        },
        setCompareSession(){
            let scs = JSON.stringify(this.compareItems)
            sessionStorage.setItem('compareList', scs)
        },
        mapHandler(){
            this.center.lng = this.locateArr[0].lng
            this.center.lat = this.locateArr[0].lat
            this.zoom = 300
        }
    },
    watch: {
        filterItems: {
            handler: function(val){
                this.hotareaid = val.area.checkItems.join(',')
                this.schooltypeid = val.schoolType.checkItems.join(',')
                this.studyformid = val.studyForm.checkItems.join(',')
                this.agesectionid = val.ageGroup.checkItems.join(',')
                this.startage = val.minVal
                this.endage = val.maxVal
                this.getSchoolList()
            },
            deep: true,
            immediate: false
        },
        schoolList: {
            handler: function(val){
                this.getLocate(val)
                this.mapHandler()
            },
            deep: true
        }
    }
}
</script>

<style>

</style>



