<template>
    <div class="train-wrapper">
        <div class="banner"></div>
        <div class="common-panel-title">
            <span class="cn"></span>
            <span class="en">our classes</span>
        </div>
        <div class="classes-main">
            <ul class="classes-wrapper">
                <li class="classes-items" v-for="cl in classList" :key="cl.id">
                    <div class="classes-show">
                        <img :src="'http://47.52.167.136:1328' + cl.trainpic" alt="">
                    </div>
                    <div class="classes-name"><span>{{cl.trainname}}</span></div>
                    <div class="classes-detail-panel">
                        <div class="detail-wrap" v-html="cl.intro"></div> 
                        <div class="detail-name">{{cl.trainname}}</div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            classList: []
        }
    },
    created(){
        let that = this
        this.axios.get(this.HOST + '/trainInfo/list?limit=10&offset=0').then((response) => {
            console.log(response.data)
            this.classList = response.data.rows
        }).catch((error) => {
            console.log(error)
        })
    }
}
</script>

<style>

</style>



