<template>
    <div id="app">
        <header class="header-wrapper">
            <i class="logo"></i>
            <nav class="nav-panel">
                <ul class="nav-list left-nav">
                    <router-link tag="li" :to="ln.link" class="nav-items" v-for="(ln, index) in leftNav" :key="index"
                    :class="{'active-item': fstSelected === 0 && sndSelected === index}"
                    @click.native="fstSelected = 0; sndSelected = index; setIndex(0, index)">{{ln.name}}</router-link>
                </ul>
                <ul class="nav-list right-nav">
                    <router-link tag="li" :to="rn.link" class="nav-items" v-for="(rn, index) in rightNav" :key="index"
                    :class="{'active-item': fstSelected === 1 && sndSelected === index}"
                    @click.native="fstSelected = 1; sndSelected = index; setIndex(1, index)">{{rn.name}}</router-link>
                    <router-link tag="li" to="" class="nav-items search-item"></router-link>
                </ul>
            </nav>
        </header>
        <div class="main-wrapper">
            <router-view/>
        </div>
        <footer class="footer-wrapper">
            <div class="footer-panel">
                <div class="foot-left">
                    <div class="contact-us">
                        <span class="cn"></span>
                        <span class="en">contact us</span>
                    </div>
                    <i class="foot-logo"></i>
                    <div class="foot-info">
                        <span class="foot-title">ES</span>
                        <span class="foot-content">530A30</span>
                        <span class="foot-content">028-61333945  028-61333946 18081185693</span>
                    </div>
                    <div class="foot-info">
                        <span class="foot-title">ES</span>
                        <span class="foot-content">Unit 26 Wellington Business Park, Dukes Ride, Crowthorne, Berkshire, RG45 6LS</span>
                        <span class="foot-content">+44 (0)1344771000</span>
                    </div>
                </div>
                <div class="foot-right">
                    <div class="input-items">
                        <span></span>
                        <input type="text" v-model="contactInfo.name">
                    </div>
                    <div class="input-items">
                        <span></span>
                        <input type="text" v-model="contactInfo.email">
                    </div>
                    <div class="input-items">
                        <span></span>
                        <input type="text" v-model="contactInfo.phone">
                    </div>
                    <div class="input-items content-item">
                        <span></span>
                        <textarea type="text" rows="3" v-model="contactInfo.content"></textarea>
                    </div>
                    <button class="submit-btn"></button>
                </div>
            </div>
            <div class="copyright-panel">Copyright  2018 www.elitescheme.com All Rights Reserved ES. ICP13004617-1</div>
        </footer>
        <div class="share-wrapper">
            <i class="phone" @click.stop="contactShow = true">
                <div class="contact-panel" :class="{'contact-panel-active': contactShow}">
                    <div class="top">
                        <span></span>
                        <i class="close" @click.stop="contactShow = false"></i>
                    </div>
                    <ul class="contact-list">
                        <li class="contact-items">
                            <div class="items-title">
                                <i class="phone-icon"></i>
                                <span></span>
                            </div>
                            <span class="items-content">028-61333945</span>
                            <span class="items-content">028-61333946</span>
                        </li>
                        <li class="contact-items">
                            <div class="items-title">
                                <i class="mobile-icon"></i>
                                <span></span>
                            </div>
                            <span class="items-content">18081185693</span>
                        </li>
                    </ul>
                </div>
            </i>
            <a class="weibo" href="http://service.weibo.com/share/share.php?appkey=&title=niubiniubi&url=www.baidu.com&searchPic=false&style=simple" target="_blank"></a>
            <i class="weixin" v-if="!qcodeShow" @click="qcodeShow = true"></i>
            <v-back-top></v-back-top>
        </div>
        <div class="qcode-mask" v-if="qcodeShow" @click="qcodeShow = false">
            <div class="qcode-panel">
                <img src="./static/home/qcode.png" alt="">
                <span class="qcode-tips"></span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "App",
    data(){
        return{
            leftNav: [
                {
                    link: '/',
                    name: ''
                },
                {
                    link: '/english-education',
                    name: ''
                },
                {
                    link: '/school-search',
                    name: ''
                },
                {
                    link: '/success-case',
                    name: ''
                },
                {
                    link: '/activity-info',
                    name: ''
                }
            ],
            rightNav: [
                {
                    link: '/study-tour',
                    name: ''
                },
                {
                    link: '/train',
                    name: ''
                },
                {
                    link: '/about-us',
                    name: 'ES'
                },
                {
                    link: '',
                    name: 'VIP'
                },
            ],
            fstSelected: 0,
            sndSelected: 0,
            contactInfo: {
                name: '',
                email: '',
                phone: '',
                content: ''
            },
            qcodeShow: false,
            contactShow: false
        }
    },
    mounted(){
        let that = this
        if(sessionStorage.getItem("fstSelected")){
            that.fstSelected = parseInt(sessionStorage.getItem("fstSelected"))
        }
        if(sessionStorage.getItem("sndSelected")){
            that.sndSelected = parseInt(sessionStorage.getItem("sndSelected"))
        }
    },
    methods: {
        setIndex(f, s){
            sessionStorage.setItem("fstSelected", f)
            sessionStorage.setItem("sndSelected", s)
        }
    }
}
</script>

<style lang="scss" type="text/css"></style>



