<template>
    <div class="school-detail-wrapper">
        <div class="banner"></div>
        <div class="school-detail-main">
            <div class="breadcrumd-wrapper">
                <span class="l-title"></span>
                <v-breadcrumb separator=">">
                    <v-breadcrumb-item>
                        <router-link to="/"></router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>
                        <router-link to="/school-search"></router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>{{sDetail.schoolcn}} {{sDetail.schoolen}}</v-breadcrumb-item>
                </v-breadcrumb>
            </div>
            <div class="school-panel">
                <div class="school-logo">
                    <img :src="'http://47.52.167.136:1328' + sDetail.logo" alt="">
                </div>
                <div class="school-info">
                    <h3 class="school-name">{{sDetail.schoolcn}} {{sDetail.schoolen}}</h3>
                    <p class="school-desc">{{sDetail.schoolcharacter}}</p>
                    <div class="btn-wrapper">
                        <button class="contrast-btn"></button>
                        <button class="compare-btn"></button>
                    </div>
                </div>
            </div>
            <div class="school-shows">
                <div class="show-left">
                    <v-carousel
                    v-model="value2"
                    :arrow="'always'"
                    :dots="'none'">
                        <v-carousel-item v-for="sp in sPics" :key="sp.id">
                            <div class="show-item">
                                <img :src="'http://47.52.167.136:1328' + sp.url" alt="">
                            </div>
                        </v-carousel-item>
                        <!-- <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item> -->
                    </v-carousel>
                    <ul class="school-characteristics">
                        <li v-for="s in scList" :key="s.id">
                            <span class="title">{{s.title}}</span>
                            <span class="result">{{s.result}}</span>
                        </li>
                        <li>
                            <span class="title"></span>
                            <span class="result"><a :href="this.sDetail.link"></a></span>
                        </li>
                    </ul>
                </div>
                <div class="show-right">
                    <baidu-map class="map-view" :center="center" :zoom="zoom" @ready="getLocate" :scroll-wheel-zoom="true">
                        <bm-marker :position="center">
                            <bm-label :content="locateName" :labelStyle="{fontSize : '12px'}" :offset="{width: -50, height: 30}"/>
                        </bm-marker>
                    </baidu-map>
                </div>
            </div>
            <div class="school-details-panel">
                <ul class="detail-tabs">
                    <li v-for="(t, index) in tabList" :key="index"
                    :class="{'active':tabSelected == index}"
                    @click="tabSelected = index">{{t.name}}</li>
                </ul>
                <div class="tabs-panel">
                    <div class="tabs-item">
                        <h6 class="item-title"></h6>
                        <p class="item-content" v-html="sDetail.desccn"></p>
                    </div>
                    <div class="tabs-item">
                        <h6 class="item-title"></h6>
                        <p class="item-content" v-html="sDetail.activity"></p>
                    </div>
                    <div class="tabs-item">
                        <h6 class="item-title"></h6>
                        <p class="item-content"></p>
                    </div>
                    <div class="tabs-item">
                        <h6 class="item-title"></h6>
                        <p class="item-content" v-html="sDetail.achievement"></p>
                        <div class="achievement-panel">
                            <div class="achievement-item">
                                <span class="title">A-Level</span>
                                <div class="content">
                                    <span class="content-item">A*-A90%</span>
                                    <span class="content-item">A*-B80%</span>
                                </div>
                            </div>
                            <div class="achievement-item">
                                <span class="title">GCSE</span>
                                <div class="content">
                                    <span class="content-item">A*-A90%</span>
                                    <span class="content-item">A*-B80%</span>
                                </div>
                            </div>
                            <div class="achievement-item">
                                <span class="title"></span>
                                <div class="content">
                                    <span class="content-item">20%</span>
                                </div>
                            </div>
                            <div class="achievement-item">
                                <span class="title"></span>
                                <div class="content">
                                    <span class="content-item">20%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="tabs-item">
                        <h6 class="item-title"></h6>
                        <p class="item-content" v-html="sDetail.subject"></p>
                        <div class="subject-panel">
                            <div class="subject-item">
                                <h5 class="subject-title">A-Level</h5>
                                <p class="subject-summary" v-html="sDetail.alevel"></p>
                            </div>
                            <div class="subject-item">
                                <h5 class="subject-title">Cambridge Pre-U</h5>
                                <p class="subject-summary" v-html="sDetail.preu"></p>
                            </div>
                            <div class="subject-item">
                                <h5 class="subject-title">IB</h5>
                                <p class="subject-summary" v-html="sDetail.ib"></p>
                            </div>
                            <div class="subject-item">
                                <h5 class="subject-title"></h5>
                                <p class="subject-summary" v-html="sDetail.activity"></p>
                            </div>
                        </div>
                    </div>
                    <div class="tabs-item">
                        <h6 class="item-title"></h6>
                        <p class="item-content" v-html="sDetail.alumni"></p>
                        <div class="schoolmate-panel">
                            <div class="schoolmate-item">
                                <div class="avatar"></div>
                                <div class="schoolmate-info">
                                    <div class="name-wrap">
                                        <span class="name">Swan</span>
                                        <span class="title"></span>
                                    </div>
                                    <div class="summary"></div>
                                </div>
                            </div>
                            <div class="schoolmate-item">
                                <div class="avatar"></div>
                                <div class="schoolmate-info">
                                    <div class="name-wrap">
                                        <span class="name">Swan</span>
                                        <span class="title"></span>
                                    </div>
                                    <div class="summary"></div>
                                </div>
                            </div>
                            <div class="schoolmate-item">
                                <div class="avatar"></div>
                                <div class="schoolmate-info">
                                    <div class="name-wrap">
                                        <span class="name">Swan</span>
                                        <span class="title"></span>
                                    </div>
                                    <div class="summary"></div>
                                </div>
                            </div>
                            <div class="schoolmate-item">
                                <div class="avatar"></div>
                                <div class="schoolmate-info">
                                    <div class="name-wrap">
                                        <span class="name">Swan</span>
                                        <span class="title"></span>
                                    </div>
                                    <div class="summary"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            sDetail: {},
            value2: 0,
            scList: [],
            sPics: [],
            tabList: [
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: ''
                }
            ],
            tabSelected: 0,
            center: {
                lng: 0,
                lat: 0
            },
            zoom: 200,
            locateName: ''
        }
    },
    created(){
        let that = this

        // id
        this.axios.get(this.HOST + '/school/list?limit=10&offset=0&id=' + this.$route.query.id).then((response) => {
            console.log(response.data)
            this.sDetail = response.data.rows[0]
            this.getInfo()
            this.getLocate()
        }).catch((error) => {
            console.log(error)
        })
    },
    methods: {
        getInfo(){
            let o = this.sDetail
            this.scList = [
                {
                    title: '',
                    result: o.studyformname
                },
                {
                    title: '',
                    result: o.schooltypename
                },
                {
                    title: '',
                    result: o.startage+'-'+o.endage
                },
                {
                    title: '',
                    result: o.intlratio
                },
                {
                    title: '',
                    result: o.fee
                },
                {
                    title: '',
                    result: o.academicrank
                },
                {
                    title: '',
                    result: o.addr
                },
                {
                    title: '',
                    result: o.founding
                },
                {
                    title: '',
                    result: o.enrollnum
                },
                {
                    title: '',
                    result: o.teacherstudentratio
                },
                {
                    title: '',
                    result: o.studentsnum
                }
            ]

            let picarr = this.sDetail.pic.split(',')
            for(let i of picarr){
                let obj = {
                    url: i
                }
                this.sPics.push(obj)
            }
        },
        getLocate(){
            this.center = {
                lng: this.sDetail.lng,
                lat: this.sDetail.lat
            }
            this.zoom = 200
            this.locateName = this.sDetail.schoolcn + ' ' + this.sDetail.schoolen
        }
    }
}
</script>

<style>

</style>



