<template>
    <div class="activity-info-wrapper">
        <div class="banner"></div>
        <div class="activity-info-detail-main">
            <div class="breadcrumd-wrapper">
                <span class="l-title"></span>
                <v-breadcrumb separator=">">
                    <v-breadcrumb-item>
                        <router-link to="/"></router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>
                        <router-link to="/activity-info"></router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item v-if="type === 'activity'"></v-breadcrumb-item>
                    <v-breadcrumb-item v-else></v-breadcrumb-item>
                    <v-breadcrumb-item></v-breadcrumb-item>
                </v-breadcrumb>
            </div>
            <h1 class="activity-title">{{infoItems.name}}</h1>
            <ul class="activity-info" v-if="type === 'activity'">
                <li class="info-items">
                    <span class="item-title"></span>
                    <span class="item-content">{{infoItems.date}}</span>
                </li>
                <li class="info-items">
                    <span class="item-title"></span>
                    <span class="item-content">{{infoItems.time}}</span>
                </li>
                <li class="info-items">
                    <span class="item-title"></span>
                    <span class="item-content">{{infoItems.location}}</span>
                </li>
                <li class="info-items">
                    <span class="item-title"></span>
                    <span class="item-content">{{infoItems.cost}}</span>
                </li>
                <li class="info-items">
                    <span class="item-title"></span>
                    <span class="item-content">{{infoItems.contact}}</span>
                </li>
            </ul>
            <ul class="activity-info" v-else>
                <li class="info-items">
                    <span class="item-title"></span>
                    <span class="item-content">{{infoItems.date}}</span>
                </li>
            </ul>
            <div class="signup-panel" v-if="type === 'activity'">
                <div class="sign-items" style="width: 13%">
                    <sub>*</sub>
                    <span class="item-title"></span>
                    <v-input size="small" v-model="signupForm.parentName"></v-input>
                </div>
                <div class="sign-items" style="width: 25%">
                    <sub>*</sub>
                    <span class="item-title"></span>
                    <v-input size="small" v-model="signupForm.parentNumber.number">
                        <v-select size="small" placeholder="" style="width: 80px" slot="before" :data="codeOptions"
                        v-model="signupForm.parentNumber.areaCode"></v-select>
                    </v-input>
                </div>
                <div class="sign-items" style="width: 24%">
                    <sub>*</sub>
                    <span class="item-title"></span>
                    <v-input size="small" v-model="signupForm.city.cityName">
                        <v-select size="small" placeholder="" style="width: 60px" slot="before" :data="provinceOptions"
                        v-model="signupForm.city.province"></v-select>
                    </v-input>
                </div>
                <div class="sign-items" style="width: 15%">
                    <sub>*</sub>
                    <span class="item-title"></span>
                    <v-input-number size="small" :min="0" v-model="signupForm.personNumber"></v-input-number>
                    <span style="margin-left: 10px"></span>
                </div>
                <button class="signup-btn"></button>
            </div>
            <div class="activity-content">
                <div class="content-left">
                    <div v-html="infoItems.content"></div>
                </div>
                <div class="content-right">
                    <div class="recommend-panel panel1">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                    <div class="recommend-panel panel2">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                    <div class="recommend-panel panel3">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                    <div class="recommend-panel panel4">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'activityInfoDetail',
    data() {
        return {
            type: '',
            infoItems: {
                name: '',
                date: '',
                time: '',
                contact: '',
                location: '',
                cost: '',
                contact: '',
                content: ''
            },
            signupForm: {
                parentName: '',
                parentNumber: {
                    areaCode: 'china',
                    number: ''
                },
                city: {
                    province: 'china',
                    cityName: ''
                },
                personNumber: ''
            },
            codeOptions: [
                {
                    label: '+86',
                    value: 'china'
                },
                {
                    label: '+44',
                    value: 'england'
                }
            ],
            provinceOptions: [
                {
                    label: '',
                    value: 'china'
                },
                {
                    label: '',
                    value: 'jiangsu'
                }
            ]
        }
    },
    created(){
        let that = this
        this.type = this.$route.query.type
        let info = this.infoItems
        // 
        if(this.$route.query.type === 'activity'){
            this.axios.get(this.HOST + '/infoActivity/list?limit=10&offset=0&id='+this.$route.query.id).then((response) => {
                let obj = response.data.rows[0]
                info.name = obj.activityname
                info.date = obj.startdate+'-'+obj.enddate
                info.time = obj.deadtime
                info.location = obj.place
                info.cost = obj.cost
                info.contact = obj.contact
                info.content = obj.content
            }).catch((error) => {
                console.log(error)
            })
        }else{
            this.axios.get(this.HOST + '/infoStudy/list?limit=10&offset=0&id='+this.$route.query.id).then((response) => {
                let obj = response.data.rows[0]
                info.name = obj.infoname
                info.date = obj.updatetime
                info.content = obj.content
            }).catch((error) => {
                console.log(error)
            })
        }
    }
}
</script>

<style>

</style>



