<template>
    <div class="home-wrapper">
        <div class="slide-banner">
            <v-carousel v-model="banneValue">
                <v-carousel-item>
                    <div class="banner-items"></div>
                </v-carousel-item>
                <v-carousel-item>
                    <div class="banner-items"></div>
                </v-carousel-item>
                <v-carousel-item>
                    <div class="banner-items"></div>
                </v-carousel-item>
            </v-carousel>
        </div>
        <div class="activity-section">
            <div class="activity-tabs-panel">
                <div class="activity-tabs">
                    <div class="tab-items" :class="{'selected': activitySelected == 'activities'}"
                    @click="activitySelected = 'activities'">
                        <span class="cntitle"></span>
                        <span class="entitle">ACTIVITIES</span>
                    </div>
                    <div class="tab-items"  :class="{'selected': activitySelected == 'news'}"
                    @click="activitySelected = 'news'">
                        <span class="cntitle"></span>
                        <span class="entitle">NEWS</span>
                    </div>
                </div>
            </div>
            <div class="tabs-main-wrapper" v-if="activitySelected == 'activities'">
                <div class="tabs-main">
                    <div class="items-card animate" data-ani="fadein-bottom" v-for="al in activityList" :key="al.id">
                        <div class="card-show">
                            <img :src="'http://47.52.167.136:1328' + al.activitypic" alt="">
                        </div>
                        <div class="card-info">
                            <h2 class="info-title">
                                {{al.activityname}}
                            </h2>
                            <p class="info-content">
                                {{al.intro}}
                            </p>
                        </div>
                        <div class="card-bootom">
                            <span class="date">{{al.startDay}}</span>
                            <span class="year">{{al.startYear}}</span>
                            <button class="arrow-btn"></button>
                        </div>
                    </div>
                </div>
                <div class="btn-wrapper">
                    <router-link tag="button" class="more-btn" to="/activity-info">MORE<i></i></router-link>
                </div>
            </div>
            <div class="tabs-main-wrapper" v-else>

            </div>
        </div>
        <div class="school-section">
            <div class="common-panel-title">
                <span class="cn"></span>
                <span class="en">recommendation</span>
            </div>
            <div class="school-main">
                <div class="school-tabs">
                    <div class="tab-items" :class="{'selected': studyformid == '1'}" @click="studyformid = '1'"></div>
                    <div class="tab-items" :class="{'selected': studyformid == '2'}" @click="studyformid = '2'"></div>
                </div>
                <div class="tabs-main-wrapper">
                    <div class="item-tabs-wrapper">
                        <ul class="item-tabs">
                            <li>
                                <span :class="{'selected': schooltypeid == ''}" @click="schooltypeid=''"></span>
                            </li>
                            <li>
                                <span :class="{'selected': schooltypeid == '1'}" @click="schooltypeid='1'"></span>
                            </li>
                            <li>
                                <span :class="{'selected': schooltypeid == '2'}" @click="schooltypeid='2'"></span>
                            </li>
                            <li>
                                <span :class="{'selected': schooltypeid == '3'}" @click="schooltypeid='3'"></span>
                            </li>
                        </ul>
                        <button class="more-btn">more<i></i></button>
                    </div>
                    <ul class="item-tab-main">
                        <li class="item-tab-card animate" data-ani="fadein-bottom" v-for="s in schoolList" :key="s.id">
                            <div class="card-show">
                                <img :src="'http://47.52.167.136:1328' + s.logo" alt="">
                            </div>
                            <div class="card-bottom">
                                <div class="card-info">
                                    <div class="title-wrap">
                                        <div class="title">
                                            <span class="cn">{{s.schoolcn}}</span>
                                            <span class="en">{{s.schoolen}}</span>
                                        </div>
                                        <span class="tag">{{s.studyformname}}</span>
                                    </div>
                                    <p class="card-content">{{s.schoolcharacter}}</p>
                                </div>
                                <div class="btn-wrap">
                                    <button class="jump-btn"></button>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="success-section">
            <div class="common-panel-title">
                <span class="cn"></span>
                <span class="en">successful cases</span>
            </div>
            <div class="case-wrapper">
                <div class="case-item animate" data-ani="fadein-bottom" v-for="cl in caseList" :key="cl.id">
                    <div class="case-photo">
                        <img :src="'http://47.52.167.136:1328' + cl.casepic" alt="">
                    </div>
                    <div class="case-detail">
                        <div class="name">{{cl.caseen}}</div>
                        <div class="school">
                            <span class="item-title"></span>
                            <span class="item-result">{{cl.school}}</span>
                        </div>
                        <div class="age">
                            <span class="item-title"></span>
                            <span class="item-result">{{cl.age}}</span>
                        </div>
                        <div class="thoughts">
                            <p>
                                {{cl.intro}}
                            </p>
                        </div>
                    </div>
                    <div class="case-summary">
                        <span class="name">{{cl.caseen}}</span>
                        <span class="school">{{cl.school}}</span>
                        <span class="age">{{cl.age}}</span>
                    </div>
                </div>
            </div>
            <div class="btn-wrapper">
                <button class="more-btn">MORE<i></i></button>
            </div>
        </div>
        <div class="superiority-section">
            <div class="common-panel-title">
                <span class="cn"></span>
                <span class="en">superiority</span>
            </div>
            <div class="superiority-main">
                <div class="superiority-wrapper">
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon1"></i>
                        <span class="number">10W+</span>
                        <span class="info"></span>
                    </div>
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon2"></i>
                        <span class="number">600+</span>
                        <span class="info">OFFER</span>
                    </div>
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon3"></i>
                        <span class="number">129</span>
                        <span class="info"></span>
                    </div>
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon4"></i>
                        <span class="number">36</span>
                        <span class="info"></span>
                    </div>
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon5"></i>
                        <span class="number">10W+</span>
                        <span class="info"></span>
                    </div>
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon6"></i>
                        <span class="number">500+</span>
                        <span class="info"></span>
                    </div>
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon7"></i>
                        <span class="number">10W+</span>
                        <span class="info"></span>
                    </div>
                    <div class="superiority-items animate" data-ani="fadein-bottom">
                        <i class="icon8"></i>
                        <span class="number">10W+</span>
                        <span class="info"></span>
                    </div>
                </div>
            </div>
        </div>
        <div class="professional-section multi-slider">
            <div class="common-panel-title">
                <span class="cn"></span>
                <span class="en">professional</span>
            </div>
            <div class="slider-wrapper">
                <v-carousel v-model="teachValue" dots="none">
                    <v-carousel-item>
                        <ul class="slider-item-wrapper">
                            <li class="slider-item" v-for="pl in professionalList" :key="pl.id">
                                <div class="item-main">
                                    <div class="photo">
                                        <img :src="'http://47.52.167.136:1328'+pl.adviserpic" alt="">
                                    </div>
                                    <div class="qcode">
                                        <img src="../static/home/qcode.png" alt="">
                                    </div>
                                    <div class="info">
                                        <div class="name">{{pl.advisercn}} {{pl.adviseren}}</div>
                                        <div class="introduction">{{pl.intro}}</div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </v-carousel-item>
                </v-carousel>
            </div>
        </div>
        <div class="certification-section multi-slider">
            <div class="common-panel-title">
                <span class="cn"></span>
                <span class="en">certification</span>
            </div>
            <div class="slider-wrapper">
                <v-carousel v-model="certificationValue" dots="none">
                    <v-carousel-item>
                        <ul class="slider-item-wrapper">
                            <li class="slider-item">
                                <i></i>
                                <span></span>
                            </li>
                            <li class="slider-item">
                                <i></i>
                                <span></span>
                            </li>
                            <li class="slider-item">
                                <i></i>
                                <span></span>
                            </li>
                            <li class="slider-item">
                                <i></i>
                                <span></span>
                            </li>
                        </ul>
                    </v-carousel-item>
                </v-carousel>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            banneValue: 0,
            activitySelected: 'activities',
            schoolSelected: 0,
            sItemSelected: 0,
            certificationValue: 0,
            teachValue: 0,
            activityList: [
                {
                    activityname: '',
                    activitypic: '',
                    intro: '',
                    startdate: '',
                    enddate: ''
                }
            ],
            caseList: [
                {
                    casepic: '',
                    casecn: '',
                    caseen: '',
                    school: '',
                    age: '',
                    intro: '',
                    speech: ''
                }
            ],
            professionalList: [
                {
                    adviserpic: '',
                    advisercn: '',
                    adviseren: '',
                    intro: ''
                }
            ],
            schoolList: [],
            schooltypeid: '',
            studyformid: '1',
        }
    },
    methods: {
        handleAnimate() {
	  		let top = pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
	  		let vh = document.documentElement.clientHeight;
	  		let dom = document.querySelectorAll(".animate");
	  		[].slice.call(dom).forEach(v => {
	  			if(top + vh > v.offsetTop){
	  				var delay = v.dataset.delay;
	  				if(delay){
	  					setTimeout(() => {
	  						v.style.opacity = 1;
		  					v.classList.add(v.dataset.ani)
		  				}, delay)
	  				}else{
	  					v.style.opacity = 1;
	  					v.classList.add(v.dataset.ani)
	  				}
	  			}else{
	  				v.classList.remove(v.dataset.ani)
	  				v.style.opacity = 0;
	  			}
	  		})
        },
        getDate(arr){
            let temp
            for(let i of arr){
                temp = i.startdate.split('-')
                i.startYear = temp[0]
                i.startDay = temp[1] + '-' + temp[2]
            }
        },
        getSchoolList(){
            let that = this
            let schooltypeid = this.schooltypeid ? '&schooltypeid=' + this.schooltypeid : ''
            let studyformid = this.studyformid ? '&studyformid=' + this.studyformid : ''

            this.axios.get(this.HOST + '/school/list?limit=8&offset=0' + schooltypeid + studyformid).then((response) => {
                console.log(response.data)
                that.schoolList = response.data.rows
                that.schoolList = that.schoolList.splice(0, 8)
            }).catch((error) => {
                console.log(error)
            })
        }
    },
    created(){
        let that = this

        // 
        this.axios.get(this.HOST + '/infoActivity/list?limit=3&offset=0').then((response) => {
            that.activityList = response.data.rows
            that.getDate(that.activityList)
        }).catch((error) => {
            console.log(error)
        })
        
        // 
        this.axios.get(this.HOST + '/caseRecommend/list?limit=10&offset=0').then((response) => {
            that.caseList = response.data.rows
        }).catch((error) => {
            console.log(error)
        })

        // 
        this.axios.get(this.HOST + '/adviserInfo/list?limit=10&offset=0').then((response) => {
            that.professionalList = response.data.rows
        }).catch((error) => {
            console.log(error)
        })
        
        // 
        this.getSchoolList()
    },
    mounted() {
        this.$nextTick(() => {
	  		this.handleAnimate()//
	  		addEventListener('scroll', this.handleAnimate);
	  	})
    },
    watch: {
        schooltypeid(val){
            this.getSchoolList()
        },
        studyformid(val){
            this.getSchoolList()
        }
    }
}
</script>

<style>

</style>



