// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import vueBeauty from 'vue-beauty'
import axios from 'axios'
import VueAxios from 'vue-axios'
import BaiduMap from 'vue-baidu-map'

import 'vue-beauty/package/style/vue-beauty.min.css'
import './style/common.scss'
import './style/head.scss'
import './style/home.scss'
import './style/english-education.scss'
import './style/activity-info.scss'
import './style/school-search.scss'
import './style/success-case.scss'
import './style/train.scss'
import './style/school-detail.scss'
import './style/study-tour.scss'
import './style/about-us.scss'

Vue.prototype.HOST = '/api'
Vue.use(vueBeauty)
Vue.use(VueAxios, axios)
Vue.use(BaiduMap, {
  ak: 'V2myuDhzGioXpTKhcN6AgUBq'
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



