<template>
    <div class="activity-info-wrapper study-tour-wrapper">
        <div class="banner"></div>
        <div class="activity-info-main">
            <div class="left-bar">
                <ul class="activity-leftbar">
                    <li v-for="(l, index) in leftList" :key="index"
                    :class="{'active': leftbar == index}"
                    @click="leftbar = index; getContent(l.id)"><span>{{l.tourname}}</span></li>
                </ul>
            </div>
            <div class="activity-panel study-tour-panel">
                <div class="study-tour-content" v-html="tourConetnt"></div>
                <div class="signup-panel">
                    <div class="signup-line">
                        <div class="sign-items">
                            <sub>*</sub>
                            <span class="item-title"></span>
                            <v-input size="small" v-model="signupForm.parentName"></v-input>
                        </div>
                        <div class="sign-items">
                            <sub>*</sub>
                            <span class="item-title"></span>
                            <v-input size="small" v-model="signupForm.parentNumber.number">
                                <v-select size="small" placeholder="" style="width: 80px" slot="before" :data="codeOptions"
                                v-model="signupForm.parentNumber.areaCode"></v-select>
                            </v-input>
                        </div>
                    </div>
                    <div class="signup-line">
                        <div class="sign-items">
                            <sub>*</sub>
                            <span class="item-title"></span>
                            <v-input size="small" v-model="signupForm.city.cityName">
                                <v-select size="small" placeholder="" style="width: 60px" slot="before" :data="provinceOptions"
                                v-model="signupForm.city.province"></v-select>
                            </v-input>
                        </div>
                        <div class="sign-items">
                            <sub>*</sub>
                            <span class="item-title"></span>
                            <v-input-number size="small" :min="0" v-model="signupForm.personNumber"></v-input-number>
                            <span style="margin-left: 10px"></span>
                        </div>
                    </div>
                    <button class="signup-btn"></button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            leftList: [
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: ''
                }
            ],
            leftbar: 0,
            tourConetnt: '',
            signupForm: {
                parentName: '',
                parentNumber: {
                    areaCode: 'china',
                    number: ''
                },
                city: {
                    province: 'china',
                    cityName: ''
                },
                personNumber: ''
            },
            codeOptions: [
                {
                    label: '+86',
                    value: 'china'
                },
                {
                    label: '+44',
                    value: 'england'
                }
            ],
            provinceOptions: [
                {
                    label: '',
                    value: 'china'
                },
                {
                    label: '',
                    value: 'jiangsu'
                }
            ]
        }
    },
    created(){
        let that = this

        this.axios.get(this.HOST + '/tourType/list?limit=10&offset=0').then((response) => {
            that.leftList = response.data.rows
            that.getContent(response.data.rows[0].id)
        })
    },
    methods: {
        getContent(tid){
            this.axios.get(this.HOST + '/tourInfo/list?limit=10&offset=0&typeid=' + tid).then((response) => {
                this.tourConetnt = response.data.rows[0].content
            }).catch((error) => {
                console.log(error)
            })
        }
    }
}
</script>

<style>

</style>



