<template>
    <div class="activity-info-wrapper">
        <div class="banner"></div>
        <div class="activity-info-main">
            <div class="left-bar">
                <ul class="activity-leftbar">
                    <li :class="{'active': leftbar == 'activity'}" @click="leftbar = 'activity'"><span></span></li>
                    <li :class="{'active': leftbar == 'news'}" @click="leftbar = 'news'"><span></span></li>
                </ul>
            </div>
            <div class="activity-panel" v-if="leftbar == 'activity'">
                <ul class="activity-list">
                    <li class="activity-items" v-for="al in activityList" :key="al.id">
                        <div class="activity-show">
                            <img :src="'http://47.52.167.136:1328' + al.activitypic" alt="">
                        </div>
                        <div class="activity-content">
                            <h2 class="title">{{al.activityname}}</h2>
                            <div class="info-wrapper">
                                <div class="info-panel">
                                    <span class="info-label"></span>
                                    <span class="info-result">{{al.startdate}} - {{al.enddate}}</span>
                                </div>
                                <div class="info-panel">
                                    <span class="info-label"></span>
                                    <span class="info-result">{{al.time}}</span>
                                </div>
                                <div class="info-panel">
                                    <span class="info-label"></span>
                                    <span class="info-result">{{al.place}}</span>
                                </div>
                            </div>
                            <p class="activity-summary">{{al.intro}}</p>
                            <button class="jump-btn" @click="getActivityDetail(al)"></button>
                        </div>
                    </li>
                </ul>
                <div class="pagination-wrapper">
                    <v-pagination show-quick-jumper :value="activityPage" :total="activityTotal" @change="getMoreActivity"></v-pagination>
                </div>
            </div>
            <div class="activity-panel news-panel" v-if="leftbar == 'news'">
                <ul class="activity-list">
                    <li class="activity-items" v-for="nl in newsList" :key="nl.id" @click="getNewsDetail(nl)">
                        <div class="activity-show">
                            <img :src="'http://47.52.167.136:1328' + nl.infopic" alt="">
                        </div>
                        <div class="activity-content">
                            <h2 class="title">{{nl.infoname}}</h2>
                            <div class="info-wrapper">
                                <div class="info-panel">
                                    <span class="info-label"></span>
                                    <span class="info-label">{{nl.updatetime}}</span>
                                </div>
                            </div>
                            <p class="activity-summary">{{nl.intro}}</p>
                        </div>
                    </li>
                </ul>
                <div class="pagination-wrapper">
                    <v-pagination show-quick-jumper :total="newsTotal" :value="newsPage" @change="getMoreNews"></v-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'activityInfo',
    data() {
        return {
            leftbar: 'activity',
            activityList: [],
            activityTotal: 0,
            activityPage: 1,
            newsList: [],
            newsTotal: 0,
            newsPage: 1
        }
    },
    created(){
        this.getActivity(0)
        this.getNews(0)
    },
    methods: {
        getActivity(idx){
            let that = this

            // 
            this.axios.get(this.HOST + '/infoActivity/list?limit=10&offset='+idx).then((response) => {
                that.activityTotal = response.data.total
                that.activityList = response.data.rows
            }).catch((error) => {
                console.log(error)
            })
        },
        getMoreActivity(i){
            i = i-1
            this.getActivity(i)
        },
        getNews(idx){
            let that = this

            // 
            this.axios.get(this.HOST + '/infoStudy/list?limit=10&offset='+idx).then((response) => {
                that.newsTotal = response.data.total
                that.newsList = response.data.rows
            }).catch((error) => {
                console.log(error)
            })
        },
        getMoreNews(i){
            i = i-1
            this.getNews(i)
        },
        getActivityDetail(obj){
            this.$router.push(
                {
                    path: '/activity-info-detail',
                    query: {
                        type: 'activity',
                        id: obj.id
                    }
                }
            )
        },
        getNewsDetail(obj){
            this.$router.push(
                {
                    path: '/activity-info-detail',
                    query: {
                        type: 'news',
                        id: obj.id
                    }
                }
            )
        }
    }
}
</script>

<style>

</style>



